// 
import React, { Component } from 'react';
import { HashRouter as Router, Route } from 'react-router-dom';

// login
import Login from '../../containers/login/login';                                                      //
import RegisterPage from '../../containers/login/register/register';                                   //
import ForgetPassword from '../../containers/login/forgetPassword/forgetPassword';                     //
// 
import TaskHallPage from '../../containers/taskHall/taskHall';                                         //
import ActivityPage from '../../containers/taskHall/activity/activity';               //
import MyTaskPage from '../../containers/myTask/myTask';                                               //
import MyCenterPage from '../../containers/myCenter/myCenter';                                         //
import CashPage from '../../containers/cash/cash';                                                     //
import CashRecord from '../../containers/cash/cashRecord';                                              //
import CommissionPage from '../../containers/commission/commission';                                   //
import MyTaskDetails from '../../containers/myTaskDetails/myTaskDetails';                              //
import QuestionsTask from '../../containers/myTaskDetails/questionsTask/questionsTask';               //
  import QuestionsTasks from '../../containers/myTaskDetails/questionsTask/questionsTasks';             //
import TaskStateChild from '../../containers/myTaskDetails/taskState/taskStateChild/taskStateChild';  //
import TaskStateChilds from '../../containers/myTaskDetails/taskState/taskStateChild/taskStateChilds';  //
import LookTaskStateChild from '../../containers/myTaskDetails/taskState/taskStateChild/lookTaskStateChild';//
import GoodPingJia from '../../containers/myTaskDetails/taskState/goodPingjia/goodPingjia';           //
import TaskPingJia from '../../containers/myTaskDetails/taskState/goodPingjia/taskPingjia';           //
  import AddPingJia from '../../containers/myTaskDetails/taskState/goodPingjia/addPingjia';             //
import AppealTask from '../../containers/myTaskDetails/taskState/appealTask/appealTask';              //
  import AppealTaskDetails from '../../containers/myTaskDetails/taskState/appealTask/appealTaskDetails';  //
// 
import DfTaskNo from '../../containers/myTask/dianFuTask/dfTaskNo';                                   //  
import DfTaskOk from '../../containers/myTask/dianFuTask/dfTaskOk';                                   //  
import DfTaskChe from '../../containers/myTask/dianFuTask/dfTaskChe';                                 //  
import LiuLanTaskNo from '../../containers/myTask/liuLanTask/liuLanTaskNo';                           //  
import LiuLanTaskOk from '../../containers/myTask/liuLanTask/liuLanTaskOk';                           //  
import LiuLanTaskChe from '../../containers/myTask/liuLanTask/liuLanTaskChe';                         //  
import PingJiaTaskNo from '../../containers/myTask/pingjia/pingjiaNo';                                //  
import PingJiaTaskOk from '../../containers/myTask/pingjia/pingjiaOk';                                //  
import PingJiaTaskChe from '../../containers/myTask/pingjia/pingjiaChe';                              //  
import WenDaTaskNo from '../../containers/myTask/wenDaTask/wenDaTaskNo';                              //  
import WenDaTaskOk from '../../containers/myTask/wenDaTask/wenDaTaskOk';                              //  
import WenDaTaskChe from '../../containers/myTask/wenDaTask/wenDaTaskChe';                            //  
// 
import BuyAdmin from '../../containers/myCenter/buyAdmin/buyAdmin';                                   //
  import BindTaobao from '../../containers/myCenter/buyAdmin/taobao/taobao';                            //
    import Correct_taobao from '../../containers/myCenter/buyAdmin/taobao/correct_taobao';                //
  import BindJingdong from '../../containers/myCenter/buyAdmin/jingdong/jingdong';                      //
    import Correct_jingdong from '../../containers/myCenter/buyAdmin/jingdong/correct_jingdong';          //
  import BindPinduoduo from '../../containers/myCenter/buyAdmin/pinduoduo/pinduoduo';                   //
    import Correct_pinduoduo from '../../containers/myCenter/buyAdmin/pinduoduo/correct_pinduoduo';       //
  import BindWinpinhui from '../../containers/myCenter/buyAdmin/weipinhui/weipinhui';                   //
    import Correct_weipinhui from '../../containers/myCenter/buyAdmin/weipinhui/correct_weipinhui';       //
  import BindWykl from '../../containers/myCenter/buyAdmin/wykl/wykl';                                  //
    import Correct_wykl from '../../containers/myCenter/buyAdmin/wykl/correct_wykl';                      //
import AllCenter from '../../containers/myCenter/callCenter/allCenter';                               //
import Personal from '../../containers/myCenter/personal/personal';                                   //
import ShenSu from '../../containers/myCenter/shenSu/shenSu';                                         //
import TongZhi from '../../containers/myCenter/tongZhi/tongZhi';                                      //
  //  
  import TongZhiChild from '../../containers/myCenter/tongZhi/tongZhiChild/tongZhiChild';              //
import TuiJian from '../../containers/myCenter/tuiJian/tuiJian';                                      //
// 
import Account from '../../containers/myCenter/personal/account/account';                              //
  import Correct_Account from '../../containers/myCenter/personal/account/correct_account';               //
import LoginPassword from '../../containers/myCenter/personal/loginPassword/loginPassword';            //
import TxPassword from '../../containers/myCenter/personal/txPassword/txPassword';                     //
import XgQQ from '../../containers/myCenter/personal/xgQQ/xgQQ';                                       //QQ
import Certification from '../../containers/myCenter/personal/certification/certification';            //
import Bank from '../../containers/myCenter/personal/bank/bank';                                       //

//
const GetBaidu = props => {
  let children = props.children;
  var _hmt = _hmt || [];
  (function() {
    var hm = document.createElement("script");
    hm.src = "https://hm.baidu.com/hm.js?4c7eff31f74db00828abda6a74c101ea";
    var s = document.getElementsByTagName("script")[0];
    s.parentNode.insertBefore(hm, s);
  })();
  return children;
};

class RouteTabComponent extends Component {
  render() {
    return(
      <Router>
        <div>
          <GetBaidu>
            {/* login */}
            <Route exact path="/" component={Login} />
            <Route path="/registerPage" component={RegisterPage}/>
            <Route path="/forgetPassword" component={ForgetPassword}/>
            {/*  */}
            <Route path="/taskHallPage" component={TaskHallPage} />
            <Route path="/activity" component={ActivityPage} />
            <Route path="/myTask" component={MyTaskPage} />
            <Route path="/myCenter" component={MyCenterPage} />
            <Route path="/cash" component={CashPage} />
            <Route path="/cashRecord" component={CashRecord} />
            <Route path="/commission" component={CommissionPage} />
            <Route path="/myTaskDetails" component={MyTaskDetails} />
            <Route path="/questionsTask" component={QuestionsTask} />
            <Route path="/questionsTasks" component={QuestionsTasks}/>
            {/* <Route path="/myTaskDetails/:id" component={MyTaskDetails} />     {/* */}
            <Route path="/taskStateChild" component={TaskStateChild}/>
            <Route path="/taskStateChilds" component={TaskStateChilds}/>
            <Route path="/lookTaskStateChild" component={LookTaskStateChild}/>
            <Route path="/goodPingJia" component={GoodPingJia} />
            <Route path="/taskPingjia" component={TaskPingJia} />
            <Route path="/addPingJia" component={AddPingJia}/>
            <Route path="/appealTask" component={AppealTask} />
            <Route path="/appealTaskDetails" component={AppealTaskDetails} />
            {/*  */}
            {/*  */}
            <Route path="/dfTaskNo" component={DfTaskNo} />
            <Route path="/dfTaskOk" component={DfTaskOk} />
            <Route path="/dfTaskChe" component={DfTaskChe} />
            {/*  */}
            <Route path="/liuLanTaskNo" component={LiuLanTaskNo} />
            <Route path="/liuLanTaskOk" component={LiuLanTaskOk} />
            <Route path="/liuLanTaskChe" component={LiuLanTaskChe} />
            {/*  */}
            <Route path="/pingjiaNo" component={PingJiaTaskNo} />
            <Route path="/pingjiaOk" component={PingJiaTaskOk} />
            <Route path="/pingjiaChe" component={PingJiaTaskChe} />
            {/*  */}
            <Route path="/wenDaTaskNo" component={WenDaTaskNo}/>
            <Route path="/wenDaTaskOk" component={WenDaTaskOk}/>
            <Route path="/wenDaTaskChe" component={WenDaTaskChe}/>
            {/*  */}
            <Route path="/buyAdmin" component={BuyAdmin} />
            <Route path="/taobao" component={BindTaobao}/>
            <Route path="/correct_taobao" component={Correct_taobao}/>
            <Route path="/wykl" component={BindWykl}/>
            <Route path="/correct_wykl" component={Correct_wykl}/>
            <Route path="/jingdong" component={BindJingdong}/>
            <Route path="/correct_jingdong" component={Correct_jingdong}/>
            <Route path="/pinduoduo" component={BindPinduoduo}/>
            <Route path="/correct_pinduoduo" component={Correct_pinduoduo}/>
            <Route path="/weipinhui" component={BindWinpinhui}/>
            <Route path="/correct_weipinhui" component={Correct_weipinhui}/>
            <Route path="/allCenter" component={AllCenter} />
            <Route path="/personal" component={Personal} />
            <Route path="/shenSu" component={ShenSu} />
            <Route path="/tongZhi" component={TongZhi} />
            <Route path="/tuiJian" component={TuiJian} />
            {/*  */}
            <Route path="/account" component={Account} />
            <Route path="/correct_account" component={Correct_Account}/>
            <Route path="/loginPassword" component={LoginPassword} />
            <Route path="/txPassword" component={TxPassword} />
            <Route path="/xgQQ" component={XgQQ} />
            <Route path="/certification" component={Certification} />
            <Route path="/bank" component={Bank} />
            {/*    */}
            <Route path="/tongZhiChild" component={TongZhiChild} />
          </GetBaidu>
        </div>
      </Router>
    )
  }
}

export default RouteTabComponent
